/* ---------------- 공지박스 애니메이션 -------------- */

@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/5.0.0/normalize.min.css");

#information {
  position: absolute;
  top: 45%;
  left: 30%;
  width: 700px;
  height: 200px;
  background-color: #e9e0d9;
  opacity: 0.9;
  z-index: 10;
  transform: translateX(-1500px);
  transform: scale(0);
  animation: appear 15s 0.5s;
  z-index: 99999999;

  /*    animation:appear2 2.5s 1;*/
}

#information span {
  position: absolute;
  width:600px;
  top: 35%;
  left:10%;
  z-index: 20;
  font-size: 24px;
  font-weight: 200;
  opacity: 1;
  font-family: "나눔 고딕";
  text-align: center;
}

@keyframes appear {
  0% {
      transform: translateX(1500px);
  }
  80% {
      transform: translateX(0px);
  }
  95% {
      transform: translateX(0px);
  }
  98% {
      transform: translateX(20px);
  }
  100% {
      transform: translateX(-1500px);
  }
}

@keyframes appear2 {
  0% {
      transform: scale(1);
  }
  80% {
      transform: scale(1);
  }
  100% {
      transform: scale(0);
  }
}


/* toggle navigation css*/

#toggle {
  float: right;
  display: inline-block;
  width: 30px;
  height: 30px;
  z-index: 10;
  position: absolute;
  top: 3%;
  right: 3%;
}

#toggle span:after,
#toggle span:before {
  content: "";
  position: absolute;
  left: 0;
  top: -9px;
}

#toggle span:after {
  top: 9px;
}

#toggle span {
  position: relative;
  display: inline-block;
}

#toggle span,
#toggle span:after,
#toggle span:before {
  width: 100%;
  height: 5px;
  background-color: #888;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 3px;
}

/* on activation */

#toggle.on span {
  background-color: transparent;
}

#toggle.on span:before {
  transform: rotate(45deg) translate(5px, 5px);
}

#toggle.on span:after {
  transform: rotate(-45deg) translate(7px, -8px);
}

#toggle.on+#menu {
  opacity: 1;
  visibility: visible;
}

#menu {
  z-index: 100;
  margin-top: 4%;
  margin-right: 4%;
  float: right;
  position: relative;
  color: #999;
  width: 200px;
  padding: 10px;
  font-family: sans-serif;
  text-align: center;
  border-radius: 4px;
  background: transparent;
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s;
}

#menu:after {
  position: absolute;
  top: -15px;
  left: 95px;
  content: "";
  display: block;

  /*화살표
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-bottom: 20px solid white;*/
}

#menu ul,
#menu li,
#menu li a {
  list-style: none;
  display: block;
  margin: 10px;
  padding: 0;
}

#menu li a {
  font-size: 20px;
  padding: 10px;
  color: #fff;
  text-decoration: none;
  transition: all .2s;
  cursor: pointer;
}

#menu li a:hover,
#menu li a:focus {
  background: #eee;
  color: #000;
  cursor: pointer;
}

#photos {
